import {  
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	methods:{
		...mapMutations(['logout']),

		navTo(type){
			switch(type){
				case 1:
				uni.redirectTo({
					url: '/pages/user/personal'
				})
				break;
				case 2:
				uni.clearStorage();
				uni.showModal({
				    title: '提示',
				    content: '缓存清除成功,请删除小程序重新打开!',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
				break;
				default:
				uni.redirectTo({
					url: type
				})
				break;
			}
		},
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
						uni.clearStorage();
			    		this.logout();
			    		setTimeout(()=>{
			    			uni.navigateBack();
			    		}, 200)
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
